<template>
  <section class="Resume_Div_Wrap row">
    <article v-bind:class="['WrapContent_Div_Audio',audio ? 'activeClass' : '']" @click="AutionPlay"></article>
    <canvas id="backgroundCanvas">您的浏览器不支持 HTML5 canvas 标签。</canvas>
    <div class="WrapContent_Div_Title row">
      <div
        v-show="(!resume.oneself_switch)&&(!resume.skill_switch)&&(!resume.production_switch)&&(!resume.information_switch)"
        class="WrapTitle_Div_Index">
        <p>
          Hello, I'm QinPengXing
        </p>
        <p>
          {{resume.content}}
        </p>
      </div>

      <div v-show="resume.skill_switch" class="WrapTitle_Div_Skill row" style="width:40rem;height: 40rem"
           @mouseenter="ShowEl_Even('开发技能')" @mouseleave="HideEl_Even('开发技能')"></div>

      <div v-show="resume.oneself_switch" class="WrapTitle_Div_OneSelf" @mouseenter="ShowEl_Even('关于自己')"
           @mouseleave="HideEl_Even('关于自己')">
        <div class="col-6">
          <img src="../../static/img/yuan.gif"/>
        </div>
        <div class="col-6">
          <p>姓 名：覃朋星</p>

          <p>年 龄：25</p>

          <p>性 别: 男</p>

          <p>经 验：一年</p>

          <p>岗 位：前端开发</p>

        </div>
      </div>

      <div v-show="resume.production_switch" class="WrapTitle_Div_Production" @mouseenter="ShowEl_Even('我的作品')"
           @mouseleave="HideEl_Even('我的作品')">
        <iframe src="https://qq754958990.github.io/data-analysis-web/dist/index.html#/" frameborder="0"></iframe>
        <iframe src="https://qq754958990.github.io/flexbox/dist/index.html#/" frameborder="0"></iframe>
      </div>

      <div v-show="resume.information_switch" class="WrapTitle_Div_Information" @mouseenter="ShowEl_Even('联系方式')"
           @mouseleave="HideEl_Even('联系方式')">
        <div>
          <img src="../../static/img/qq_img.jpg">
        </div>
        <p>What's so bad about being a happy salted fish?</p>
        <div>
          <div class="col-4">
            <p><i>1.</i> TELEPHONE</p>
            <p> +86-132-6590-4980</p>
          </div>
          <div class="col-4">
            <p><i>2.</i> ADDRESS</p>
            <p>Guangdong,GuangZhou
              广东 • 广州</p>
          </div>
          <div class="col-4">
            <p><i>3.</i> EMAIL</p>
            <p>754958990@qq.com</p>
          </div>
        </div>
      </div>
    </div>
    <article class="WrapContent_Div_Property">
      <div  class="ContentProperty_Div_Item">
        <img src="../../static/img/ziji.gif"/>
        <p>
          关于自己
        </p>
      </div>

      <div  class="ContentProperty_Div_Item">
        <img src="../../static/img/qiuzhi.gif"/>
        <p>
          联系方式
        </p>
      </div>

      <div  class="ContentProperty_Div_Item">
        <img src="../../static/img/yishi.gif"/>
        <p>
          开发技能
        </p>
      </div>

      <div  class="ContentProperty_Div_Item">
        <img src="../../static/img/jingli.gif"/>
        <p>
          我的作品
        </p>
      </div>
    </article>
    <audio id="music1" autoplay loop>
      <source src="../../static/audio/bgm.mp3" type="audio/mpeg">
      您的浏览器不支持 audio 元素。
    </audio>
  </section>
</template>

<script>
  import echarts from 'echarts';

  export default {
    name: 'resume',
    data() {
      return {
        audio: true,
        lines: [],
        myChart: null,
        hideEL_id: 89757,
        resume: {
          content: '一',
          property: [
            {
              icon: '../static/img/ziji.gif',
              content: '关于自己',
            },
            {
              icon: '../static/img/qiuzhi.gif',
              content: '联系方式',
            },
            {
              icon: '../static/img/yishi.gif',
              content: '开发技能',
            },
            {
              icon: '../static/img/jingli.gif',
              content: '我的作品',
            },
          ],
          oneself_str: ``,
          skill_switch: false,
          oneself_switch: false,
          production_switch: false,
          information_switch: false,
        },
      };
    },
    methods: {
      Bg1() {
        return '#' + Math.floor(Math.random() * 256).toString(10);
      },
      Bg2() {
        return '#' + Math.floor(Math.random() * 0xffffff).toString(16);
      },
      Bg3() {
        let r = Math.floor(Math.random() * 256);
        let g = Math.floor(Math.random() * 256);
        let b = Math.floor(Math.random() * 256);
        return 'rgb(' + r + ',' + g + ',' + b + ')';
      },
      AutionPlay() {
        let audio = document.getElementById('music1');
        if (audio !== null) {
          //检测播放是否已暂停.audio.paused 在播放器播放时返回false.
          if (audio.paused) {
            audio.play();//audio.play();// 这个就是播放
            this.audio = true;
          } else {
            audio.pause();// 这个就是暂停
            this.audio = false;
          }
        }
      },
      MoveEl(el_name) {
        const _self = this;
        const client = this.GetClient();
        let move_els = document.getElementsByClassName(el_name);
        for (let i = 0; i < move_els.length; i++) {
          let move_el = move_els[i];

          let c_width = client.width - move_el.clientWidth * 1.6,
            c_height = client.height - move_el.clientHeight * 1.6;

          move_el.style.left = _self.RandomNum(0, c_width) + 'px';
          move_el.style.top = _self.RandomNum(0, c_height) + 'px';

          let long_id = 89157,
            operation_num = this.RandomNum(1, 4);

          let move = function() {
            let left = move_el.offsetLeft;
            let top = move_el.offsetTop;

            if ((left > 0 && left < c_width) && (top > 0 && top < c_height)) {
              switch (operation_num) {
                case 1:
                  move_el.style.top = top - operation_num + 'px';
                  break;
                case 2:
                  move_el.style.top = top + operation_num + 'px';
                  break;
                case 3:
                  move_el.style.left = left - operation_num + 'px';
                  break;
                case 4:
                  move_el.style.left = left + operation_num + 'px';
                  break;

              }
            } else {

              if (left <= 0) {
                move_el.style.left = 1 + 'px';
              }

              if (left >= c_width) {
                move_el.style.left = c_width - 1 + 'px';
              }

              if (top <= 0) {
                move_el.style.top = 1 + 'px';
              }

              if (top >= c_height) {
                move_el.style.top = c_height - 1 + 'px';
              }

              operation_num = _self.RandomNum(1, 4);
            }

            long_id = setTimeout(move);
          };

          move_el.addEventListener('mouseenter', function(e) {
            clearTimeout(long_id);
            let textConten = e.currentTarget.innerText;
            textConten = textConten.replace(/\s+/g, '');
            switch (textConten) {
              case '关于自己':
                _self.resume.oneself_switch = true;
                _self.resume.production_switch = false;
                _self.resume.skill_switch = false;
                _self.resume.information_switch = false;
                break;
              case '我的作品':
                _self.resume.oneself_switch = false;
                _self.resume.production_switch = true;
                _self.resume.skill_switch = false;
                _self.resume.information_switch = false;
                break;
              case '开发技能':
                _self.resume.oneself_switch = false;
                _self.resume.production_switch = false;
                _self.resume.skill_switch = true;
                _self.resume.information_switch = false;
                break;
              case '联系方式':
                _self.resume.oneself_switch = false;
                _self.resume.production_switch = false;
                _self.resume.skill_switch = false;
                _self.resume.information_switch = true;
                break;
            }
          });

          move_el.addEventListener('mouseleave', function(e) {
            let textConten = e.currentTarget.innerText;
            textConten = textConten.replace(/\s+/g, '');
            _self.hideEL_id = setTimeout(function() {
              switch (textConten) {
                case '关于自己':
                  _self.resume.oneself_switch = false;
                  break;
                case '我的作品':
                  _self.resume.production_switch = false;
                  break;
                case '开发技能':
                  _self.resume.skill_switch = false;
                  break;
                case '联系方式':
                  _self.resume.information_switch = false;
                  break;
              }
            }, 3000);
            move();
          });

          move();
        }
      },
      DrawPie(el_name) {
        // 基于准备好的dom，初始化echarts实例
        this.myChart = echarts.init(document.getElementsByClassName(el_name)[0]);

        let option = {
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)',
          },
          series: [
            {
              name: '能量指数',
              type: 'pie',
              selectedMode: 'single',
              radius: [0, '10%'],
              label: {
                normal: {
                  position: 'inner',
                },
              },
              labelLine: {
                normal: {
                  show: false,
                },
              },
              data: [
                {value: 3, name: 'js'},
                {value: 3, name: 'html'},
                {value: 3, name: 'css'},
              ],
            },
            {
              name: '能量指数',
              type: 'pie',
              radius: ['20%', '30%'],
              label: {
                normal: {
                  formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
                  borderColor: '#aaa',
                  borderWidth: 1,
                  borderRadius: 4,
                  rich: {
                    a: {
                      color: '#999',
                      lineHeight: 22,
                      align: 'center',
                    },
                    hr: {
                      borderColor: '#aaa',
                      width: '100%',
                      borderWidth: 0.5,
                      height: 0,
                    },
                    b: {
                      fontSize: 16,
                      lineHeight: 33,
                    },
                    per: {
                      color: '#eee',
                      backgroundColor: '#334455',
                      padding: [2, 4],
                      borderRadius: 2,
                    },
                  },
                },
              },
              data: [
                {value: 2, name: 'vue'},
                {value: 2, name: 'webpack'},
                {value: 1, name: 'sass'},
                {value: 1, name: 'typescript'},
                {value: 1, name: 'echarts'},
                {value: 1, name: 'jest'},
                {value: 1, name: 'react'},
                {value: 1, name: 'nodejs'},
                {value: 1, name: 'c/c++'},
                {value: 1, name: 'linux'},
              ],
            },
          ],
        };

        // 使用刚指定的配置项和数据显示图表。
        this.myChart.setOption(option);
      },
      ShowEl_Even(str) {
        clearTimeout(this.hideEL_id);
      },
      HideEl_Even(str) {
        switch (str) {
          case '关于自己':
            this.resume.oneself_switch = false;
            break;
          case '我的作品':
            this.resume.production_switch = false;
            break;
          case '开发技能':
            this.resume.skill_switch = false;
            break;
          case '联系方式':
            this.resume.information_switch = false;
            break;
        }
      },
      DrawStar(cxt, r, R, x, y, rot, borderWidth, borderStyle, fillStyle) {
        cxt.beginPath();
        for (var i = 0; i < 5; i++) {
          cxt.lineTo(Math.cos((18 + 72 * i - rot) / 180 * Math.PI) * R + x,
            -Math.sin((18 + 72 * i - rot) / 180 * Math.PI) * R + y);
          cxt.lineTo(Math.cos((54 + 72 * i - rot) / 180 * Math.PI) * r + x,
            -Math.sin((54 + 72 * i - rot) / 180 * Math.PI) * r + y);
        }
        cxt.closePath();

        cxt.lineWidth = borderWidth;
        cxt.strokeStyle = borderStyle;
        cxt.fillStyle = fillStyle;

        cxt.fill();
        cxt.stroke();
      },
      DrawImage(x, y) {
        let context = this.SetContext();
        //创建新的图片对象
        let img = new Image();
        //指定图片的URL
        img.src = 'http://www.365mini.com/image/google_logo.png';
        //浏览器加载图片完毕后再绘制图片
        img.onload = function(x, y, width, height) {
          //以Canvas画布上的坐标(10,10)为起始点，绘制图像
          context.DrawImage(img, x, y, width, height);
        };

      },
      RandomNum(minNum, maxNum) {
        switch (arguments.length) {
          case 1:
            return parseInt(Math.random() * minNum + 1, 10);
            break;
          case 2:
            return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
            break;
          default:
            return 0;
            break;
        }
      },
      GetClient() {
        if (window.innerHeight !== undefined) {
          return {
            'width': window.innerWidth,
            'height': window.innerHeight,
          };
        } else if (document.compatMode === 'CSS1Compat') {
          return {
            'width': document.documentElement.clientWidth,
            'height': document.documentElement.clientHeight,
          };
        } else {
          return {
            'width': document.body.clientWidth,
            'height': document.body.clientHeight,
          };
        }
      },
      SetContext() {

        let el = document.getElementById('backgroundCanvas');

        let client = this.GetClient();
        el.width = client.width;
        el.height = client.height;

        return el.getContext('2d');
      },
      Animation() {
        const _self = this;

        const client = this.GetClient();
        let c_width = client.width, c_height = client.height;

        const context = this.SetContext();

        requestAnimationFrame(function init() {
          context.clearRect(0, 0, c_width * 1.2, c_height * 1.2); // clear canvas

          for (let i = 0; i <= 66; i++) {
            let item = _self.lines[i];
            if (item) {
              if (item.x <= c_width && item.y <= c_height) {
                item.x = item.x + _self.RandomNum(-2, 2);
                item.y = item.y + _self.RandomNum(0, 3);
              } else {
                item.x = _self.RandomNum(0, c_width);
                item.y = _self.RandomNum(0, c_height / 2);
              }

            } else {
              item = _self.lines[i] = {
                x: _self.RandomNum(0, c_width),
                y: _self.RandomNum(0, c_height / 2),
                color: _self.Bg3(),
              };
            }

            _self.DrawStar(context, 15, 7, item.x, item.y, 180, 2, _self.Bg2(), item.color);
          }

          requestAnimationFrame(init);
        });
      },
    },
    mounted() {
      this.Animation();
      this.DrawPie('WrapTitle_Div_Skill');
      this.MoveEl('ContentProperty_Div_Item');

      let i = 0;
      let show_str = '一个做前端开发的好家伙！';
      setTimeout(function start() {
        if (i === show_str.length) {
          self.resume.content = '';
          i = 0;
        } else {
          i++;
        }
        self.resume.content += show_str.charAt(i);

        setTimeout(start, 500);
      });

      const self = this;
      window.onresize = function() {
        self.SetContext();
        self.myChart.resize();
      };

    },
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @font-face {
    font-family: myFirstFont;
    src: url('../../static/fonts/SketchFineSerif.otf')
  }

  @keyframes scaleDraw { /*定义关键帧、scaleDrew是需要绑定到选择器的关键帧名称*/
    0% {
      transform: scale(1); /*开始为原始大小*/
    }
    25% {
      transform: scale(1.1); /*放大1.1倍*/
    }
    50% {
      transform: scale(1);
    }
    75% {
      transform: scale(1.1);
    }
  }

  @keyframes border_color {
    0% {
      border-color: red;
    }
    25% {
      border-color: yellow;
    }
    50% {
      border-color: blue;
    }
    100% {
      border-color: green;
    }
  }

  @keyframes color {
    0% {
      color: red;
      text-shadow: 0.5rem 0.5rem 0.5rem red;
    }
    25% {
      color: yellow;
      text-shadow: 0.5rem 0.5rem 0.5rem yellow;
    }
    50% {
      color: blue;
      text-shadow: 0.5rem 0.5rem 0.5rem blue;
    }
    100% {
      color: green;
      text-shadow: 0.5rem 0.5rem 0.5rem green;
    }
  }

  @keyframes rotating {
    from {
      transform: rotate(0)
    }
    to {
      transform: rotate(360deg)
    }
  }

  @mixin start_animation($name) {
    animation-name: $name;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }

  canvas {
    width: 100%;
    height: 100%;
    background: url("../../static/img/bj.gif");
    -webkit-animation-name: scaleDraw; /*关键帧名称*/
    -webkit-animation-timing-function: ease-in-out; /*动画的速度曲线*/
    -webkit-animation-iteration-count: infinite; /*动画播放的次数*/
    -webkit-animation-duration: 5s; /*动画所花费的时间*/
  }

  .activeClass {
    animation: rotating 6s linear infinite
  }

  .Resume_Div_Wrap {
    height: 100%;
    text-align: center;
    font-size: 1.3rem;
    font-family: cursive;
    .WrapContent_Div_Audio {
      position: absolute;
      z-index: 9;
      left: 2rem;
      top: 2rem;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
      background-image: url("../../static/img/autio.png");
      background-size: 100% 100%;
    }
    .WrapContent_Div_Title {
      position: absolute;
      width: 50%;
      color: white;
      .WrapTitle_Div_Index {
        @include start_animation(color);
        p:first-of-type {
          font-family: myFirstFont;
        }
        p:last-of-type {
          font-weight: lighter;
        }
      }
      .WrapTitle_Div_OneSelf {
        width: 23rem;
        @include start_animation(color);
        img {
          width: 10rem;
          margin-top: 1rem;
          border-radius: 15px;
          transition: width 2s, height 2s, transform 2s;
        }

        img:hover {
          transform: rotate(180deg);
        }
        p {
          text-align: left;
        }
      }

      .WrapTitle_Div_Production {
        width: 35rem;
        height: 25rem;
        iframe {
          width: 70%;
          height: 70%;
          transition: width 2s, height 2s;
        }
        iframe:first-of-type {
          position: relative;
          z-index: 6;
          &:hover {
            width: 100%;
            height: 100%;
            z-index: 9;
          }
        }
        iframe:last-of-type {
          position: relative;
          z-index: 3;
          top: -15rem;
          left: 3rem;
          &:hover {
            width: 100%;
            height: 100%;
            z-index: 9;
          }
        }
      }
      .WrapTitle_Div_Information {
        font-family: myFirstFont;
        @include start_animation(color);
        div {
          padding: 0.2rem;
        }
        img {
          width: 6rem;
          height: 6rem;
          padding: 0.2rem;
          margin: 1rem;
          border: 0.1rem solid white;
          border-radius: 50%;
          @include start_animation(border_color);
        }

      }
    }
    .ContentProperty_Div_Item {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 6rem;
      height: 7rem;
      border: 0.2rem solid white;
      border-radius: 8px;
      padding: 1rem;
      @include start_animation(border_color);
      &:hover {
        p {
          @include start_animation(color);
        }
      }
      img {
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
      }
      p {
        margin-top: 0.5rem;
        color: white;
        font-size: 0.8rem;
      }
    }

  }
</style>



